.container h4 {
  padding: 20px;
}

.inventory-alert {
  background: #ffe591;
  padding: 15px;
  border-radius: 5px;
  max-width: 500px;
  width: 100%;
  margin: auto;
}

.inventory-alert p {
  margin-bottom: 0;
}

.showTab-enter {
  opacity: 0;
}

.showTab-enter-active {
  opacity: 1;
  transition: all 500ms;
}
